import { Offcanvas } from 'bootstrap';
import React, { useState, useEffect, useCallback } from 'react';
import { connect } from 'react-redux';
import Navbar from '../Navbar';
import "./index.scss";

const mapStateToProps = (state) => ({

});
const mapActionsToProps = {

}
const customIdGenerator = (length = 10) => {
    let result = 'top_menu_';
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    for (let i = 0; i < length; i++) {
        result += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return result;
}
const offcanvasCreator = (customId) => {
    const offcanvas = document.getElementById(customId);
    Offcanvas.getOrCreateInstance(offcanvas, {
        placement: 'top',
        flip: true,
        show: true,
        autoHide: true
    });
}

const OffcanvasTopMenu = (props) => {
    const { children } = props;
    const customId = customIdGenerator();
    const togglerId = customId + "_toggler";
    const customIdBsTarget = "#" + customId;
    const toggler = () => {
        const offcanvas = document.getElementById(customId);
        const instance = Offcanvas.getInstance(offcanvas, {
            placement: 'top',
            flip: true,
            target: customIdBsTarget,
            toggler: togglerId,
            show: true,
            autoHide: true
        });
        instance.toggle()
    }
    const hide = () => {
        const offcanvas = document.getElementById(customId);
        const instance = Offcanvas.getInstance(offcanvas);
        instance.hide()
    }
    const show = () => {
        const offcanvas = document.getElementById(customId);
        const instance = Offcanvas.getInstance(offcanvas);
        instance.show()
    }
    useEffect(() => {
        offcanvasCreator(customId)
    }, [])
    return (
        <div>
            <div>
                <Navbar toggle={(e) => {
                    toggler();
                }} hide={(e) => {
                    hide()
                }} show={(e) => {
                    show();
                }} />
                <div className="offcanvas offcanvas-top offcanvas-menu" id={customId}>
                    <div
                        onMouseOut={(e) => {
                            hide()
                        }}
                        className="offcanvas-body offcanvas-content">
                        <div style={{ background: "red", width: "calc(100% - 10px)", height: "100%" }}>

                        </div>
                    </div>
                    {/* <button className="btn btn-primary" type="button" id={togglerId} data-bs-toggle="offcanvas" data-bs-target={customIdBsTarget} aria-controls="offcanvasTop">Toggle top offcanvas</button> */}
                    {/* <div className="offcanvas offcanvas-top offcanvas-menu" tabIndex="-1" id={customId} aria-labelledby="offcanvasTopLabel">
                <div className="offcanvas-header">
                    <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                </div>
                <div className="offcanvas-body offcanvas-content">
                    {children}
                </div>
            </div> */}
                </div>
            </div>
        </div>
    )
}

const styles = {
    container: {
        flex: 1
    }
}

export default connect(mapStateToProps, mapActionsToProps)(OffcanvasTopMenu);