export const TEST = 'TEST/TEST';
export const TEST_SUCCESS = 'TEST/TEST_SUCCESS';
export const TEST_FAILED = 'TEST/TEST_FAILED';

export const testAction = (payload) => (dispatch, getStore) => {
  dispatch({
    type: TEST,
  });
  setTimeout(function () {
    dispatch({
      type: TEST_SUCCESS,
      payload: {
        test: true,
      },
    });
  }, 3000);
};

const initialState = {
  test: false,
  count: 0,
};

export default (state = initialState, action) => {
  switch (action?.type) {
    case TEST:
      return {
        ...state,
        count: state.count + 1,
      };
    case TEST_SUCCESS:
      return {
        ...state,
        ...action?.payload,
      };
    default:
      return state;
  }
};
